<template>
    <div id="draft-table">

        <!-- 面包屑 start -->
        <el-breadcrumb separator=">" class="breadcrumb">
            <el-breadcrumb-item>店铺管理</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/shop/good' }">菜品管理</el-breadcrumb-item>
            <el-breadcrumb-item>菜品草稿列表</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 面包屑 end -->

        <div class="content-box">
            <h3 class="title">菜品草稿列表</h3>

            <!-- table start -->
            <div class="table-content">
                <el-table class="table-list" :data="list" stripe height="680" @selection-change="handleSelectionChange">
                    <el-table-column min-width="80" align="center" label="编号">
                        <template slot-scope="scope">
                            <span>{{ scope.row.food_id}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column min-width="80" align="center" label="图片">
                        <template slot-scope="scope">
                            <img v-if="scope.row.food_picture !== ''" 
                            :src="imgbase_url + '/img_get.php?img=1&height=69&width=69&imgname=' + scope.row.food_picture">
                        </template>
                    </el-table-column>
                    <el-table-column min-width="93" align="center" label="名称">
                        <template slot-scope="scope">
                            <span>{{ scope.row.food_name}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column min-width="100" align="center" label="价格">
                        <template slot-scope="scope">
                            <span>{{ scope.row.food_show_price}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column min-width="80" align="center" label="商品类别">
                        <template slot-scope="scope">
                            <span>{{ scope.row.category_name}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column min-width="140" align="center" label="操作">
                        <template slot-scope="scope">
                            <span class="blue-text">
                                <router-link 
                                v-if="scope.row.type === GoodType.DISH" 
                                :to="{path:'/good',query: { foodId:scope.row.food_id}}">
                                    编辑
                                </router-link>
                                <router-link 
                                v-if="scope.row.type === GoodType.ACCESSORY" 
                                :to="{path:'/good/accesseditor',query: { foodId:scope.row.food_id}}">
                                    编辑
                                </router-link>
                                <router-link 
                                v-if="scope.row.type === GoodType.DRINK" 
                                :to="{path:'/good/accesseditor',query: { foodId:scope.row.food_id}}">
                                    编辑
                                </router-link>
                            </span>
                            <span 
                            @click="deleteFood(scope.row.food_id)" 
                            class="red-text" 
                            style="padding-left:10px;padding-top:10px;">删除</span>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- table end -->

            <!-- 分页 starat -->
            <div class="pagination-container">
                <el-pagination 
                @size-change="handleSizeChange" 
                @current-change="handleCurrentChange" 
                :current-page.sync="listQuery.page" 
                :page-sizes="[10,20,40]" 
                :page-size="listQuery.limit" 
                layout="sizes, jumper, prev, pager, next" 
                :total="total">
                </el-pagination>
            </div>
            <!-- 分页 end -->
        </div>
    </div>
</template>

<script>
import { getTableList, menuSave } from "../../api";
import { GoodType, FoodType } from "@/config/cfg";
import { GoodList } from "../../index";

export default {
  data() {
    return {
      multipleSelection: [], //存放多选选中数据
      GoodType: GoodType, //food type

      //表格
      list: null,
      total: null,
      listQuery: {
        page: 1,
        limit: 10,
        importance: undefined,
        title: undefined,
        type: undefined,
        sort: "+id"
      },

      imgbase_url: imgbase_url
    };
  },
  created() {
    this.getList();
  },
  methods: {
    //拉取草稿数据
    getList() {
      let data = {
        foodlist: 1,
        is_draft: 1,
        page_size: this.listQuery.limit,
        page_no: this.listQuery.page
      };

      getTableList(data, resp => {
        if (resp.ret === 0) {
          this.list = resp.data.list || [];
          this.total = resp.data.total;

          this.list = this.list.map(item => {
            item["food_picture"] = (item.food_img_list || [])[0] || "";

            //价格
            let foodShowPrice;
            if (item.type === GoodType.ACCESSORY) {
              foodShowPrice = item.food_price;
            } else {
              if ((item.food_price || {}).type === FoodType.YES) {
                foodShowPrice = ((item.food_price.price || [])[0] || {})
                  .original_price;
              } else if ((item.food_price || {}).type === FoodType.NO) {
                let arr = item.food_price.price || [];
                let priceArr = [];

                arr.forEach(item => {
                  priceArr.push(item.original_price);
                });

                let min = GoodList.arrayMin(priceArr);
                let max = GoodList.arrayMax(priceArr);

                foodShowPrice = min + "/" + max;
              }
            }
            item["food_show_price"] = foodShowPrice;
            return item;
          });
        } else {
          console.warn("获取草稿列表数据错误!");
        }
      });
    },

    //分页事件处理，条数和当前页数改变触发
    handleSizeChange(val) {
      this.listQuery.limit = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    },

    //删除
    deleteFood(foodId) {
      let foodArr = [];
      foodArr.push(foodId);

      let data = {
        del_food: 1,
        food_id_list: foodArr
      };
      
      menuSave(data, resp => {
        if (resp.ret === 0) {
          this.getList();
        } else {
          console.warn("操作失败！");
        }
      });
    },

    //多选
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.breadcrumb {
  margin-bottom: 14px;
}

.content-box {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.title {
  @include fc(14px, #666666);
  background-color: #f6f8fc;
  height: 40px;
  line-height: 40px;
  padding-left: 14px;
}

.table-content {
  padding: 20px 14px;

  table {
    img {
      @include wh(60px, 60px);
      vertical-align: middle;
      margin: 10px 0;
    }
  }
}

.red-text {
  @include fc(14px, #E7487E);
  cursor: pointer;
}

.blue-text {
  @include fc(14px, #4877e7);
  cursor: pointer;

  a {
    @include fc(14px, #4877e7);
  }
}

.pagination-container {
  text-align: center;
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#draft-table {
  .el-breadcrumb {
    @include fc(12px, #333333);
  }
  .el-breadcrumb__item:last-child .el-breadcrumb__item__inner {
    color: #4877e7;
  }
  .el-breadcrumb__item__inner:hover {
    color: #4877e7;
  }
  .el-pagination {
    padding-top: 30px;
    padding-bottom: 20px;
  }
  .el-pagination__jump {
    margin-left: 5px;
    margin-right: 15px;
  }
  .el-table td {
    height: 80px;
  }
}
</style>


